@import "../../theme/colors";


.login {
  .login-form {
    display: flex;
    flex-direction: row;
    justify-content: center;
  }

  .rmr-me-and-fpwd {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 6px;
    color: map-get($grays, 700);
  }

  // social login
  .login-social-section {
    display: flex;
    justify-content: center;
    flex-direction: column;
    width: 100%;
    max-width: 360px;
    height: 100%;
  }

  .divider {
    width: 1px;
    opacity: 0.8;
    background-color: map-get($grays, 600);
    margin: 0 70px;
  }

  .login-footer {
    width: 100%;
    max-width: 420px;
    margin: 20px auto 0;

    .login-privacy {
      font-size: 14px;
      color: $base-brown-color;
      text-align: center;
      line-height: 1.5;
    }
    .login-copyright {
      font-size: 12px;
      color: $base-brown-color;
      text-align: center;
      line-height: 1.5;
    }
  }
}

// Facebook style
.facebook-wrapper {
  background-color: map-get($colors, secondary);
}

.facebook-icon {
  background-color: $base-white-color;
  border-radius: 1px;
  padding: 12px 15px 13px 16px;

  & div {
    background-image: url('../../assets/images/facebook.svg');
  }
}

// Google Style
.btn-google {
  margin-top: 20px;

  .wrapper {
    background-color: map-get($colors, primary);
  }

  .google-icon {
    background-color: $base-white-color;
    border-radius: 1px;
    padding: 12px 15px 13px 16px;
  
    & div {
      background-image: url('../../assets/images/google.svg');
    }
  }
}

// Apple Style
.btn-apple {
  margin-top: 24px;

  .apple-wrapper {
    background-color: $base-black-color;
  }

  .apple-icon {
    background-color: $base-white-color;
    border-radius: 1px;
    padding: 12px 15px 13px 16px;
  
    & div {
      background-image: url('../../assets/images/apple.svg');
    }
  }
}
