@import "../../theme/colors.scss";

.btn-container {
  position: relative;
  padding: 24px 0;

  .btn {
    width: 100%;
    height: 50px;
    font-size: 20px;
    margin: 0;
    padding-top: 14px;
    padding-bottom: 14px;
    padding-inline-start: 0;
    border-radius: 30px;
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16),
      0 2px 10px 0 rgba(0, 0, 0, 0.12);
    border: none;
    outline: none;
  }

  .btn-submit {
    background: $form-button-color;
    color: $base-white-color;
  }

  .loading {
    position: absolute;
    right: 5px;
    width: 50px;
    height: auto;
  }
}
