@import "./colors.scss";

.form-title {
  font-size: 52px;
  line-height: 1;
  text-align: center;
  text-transform: uppercase;
  margin-top: 0;
  margin-bottom: 24px;
  color: $form-title;
}

.form-subtitle {
  color: $form-title;
  font-size: 24px;
  text-align: center;
  margin-bottom: 36px;
}

.form {
  display: flex;
  flex-direction: column;
  margin-top: 10px;
}

.form-border-error {
  border: 1px solid $form-error-message;
}

.checkbox-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-left: 8px;

  .label {
    position: relative;
    font-size: 14px;
    margin: 0;
    color: $text-primary-color;
    cursor: pointer;
  }

  .label:before {
    content: '';
    -webkit-appearance: none;
    background-color: transparent;
    border: 2px solid $text-primary-color;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05), inset 0px -15px 10px -12px rgba(0, 0, 0, 0.05);
    padding: 8px;
    display: inline-block;
    position: relative;
    vertical-align: middle;
    border-radius: 4px;
    cursor: pointer;
    margin-right: 8px;
    margin-bottom: 2px;
  }

  .checkbox:checked + label:after {
    content: '';
    display: block;
    position: absolute;
    top: 2px;
    left: 7px;
    width: 6px;
    height: 14px;
    border: solid $text-primary-color;
    border-width: 0 2px 2px 0;
    transform: rotate(45deg);
}

  .checkbox {
    padding: 0;
    height: initial;
    width: initial;
    margin-bottom: 0;
    display: none;
    cursor: pointer;
  }

  .error-checkbox ~ .label {
    color: $form-error-message;
  }

  .error-checkbox ~ .label:before {
    border: 2px solid $form-error-message;
  }
}
