@import "../../theme/colors.scss";

.form .input-container-error {
  margin: 0 auto 0 auto;
}

.input-container {
  position: relative;
  width: 360px;
  margin: 0 auto 24px auto;

  .input-box {
    display: flex;
    align-items: center;
    height: 44px;
    margin: 0 auto;
    font-size: 16px;
    background: $base-white-color;
    border-radius: 30px;
    box-shadow: 0 1px 6px 0 $form-input-border-shadow;
    cursor: text;
  }

  .input {
    width: 100%;
    margin: 0;
    font-size: 14px;
    padding: 13px 0 13px 40px;
    padding-inline-start: 40px;
    color: map-get($grays, 700);
    border-radius: 30px;
    outline: none;
    border: none;
  }

  .input-icon {
    position: absolute;
    left: 10px;
    height: 12px;
    margin-bottom: 1px;
    cursor: pointer;
  }
}

.border-error {
  border: 1px solid $form-error-message;
}

.error-icon {
  position: absolute;
  width: 16px;
  height: auto;
  right: 10px;

  img {
    width: 100%;
    height: 100%;
  }
}
