.date-range {
  margin-left: 4px;
  display: flex;
  position: relative;
  background: #ffffff;
  height: 48px;
  border: none;
  padding: 10px 10px 10px 30px;
  transition-duration: 0.3s;

  &:hover {
    box-shadow: 0 0 0 1px #d59c00 inset;
  }

  .date-icon {
    position: absolute;
    top: 25%;
    left: 16px;
    width: 18px;
    z-index: 1;
  }

  .input {
    width: 100%;
    max-width: 112px;
    margin: 0;
    padding: 0;
    outline: none;
    border: none;
    background-color: transparent;
    z-index: 1;
    left: 0px;
    height: 24px;
    line-height: 24px;
    vertical-align: top;
    transition-property: color;
    transition-duration: 0.3s;
    color: #000000;
    font-size: 14px;
    font-weight: 500;
    text-align: center;
  }

  .date-range-arrow {
    line-height: 24px;
    color: $base-white-color;
  }
}
