@import "../../theme/colors.scss";
@import "../../theme/layering.scss";

.notification-container {
  position: relative;
  top: 0;
  width: 100%;

  #notification-wrapper {
    position: absolute;
    top: 0;
    width: 100% !important;
    z-index: $notification-z-index;
  }

  .toast-notification {
    width: 100% !important;
  }

  & span {
    width: 100% !important;
    font-size: 18px;
    padding: 19.5px 15px !important;
    border-radius: 0 !important;
    color: $base-white-color !important;
  }
}
