@import "../../../theme/colors.scss";
@import "../../../theme/layering.scss";

.header {
  position: fixed;
  width: 100%;
  margin: 0 auto;
  padding: 0 10px;
  display: flex;
  justify-content: space-between;
  background-color: $base-white-color;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  z-index: $header-z-index;

  & h1 {
    margin: 6px 0 4px;
  }

  .logo {
    width: auto;
    height: 42px;
  }
}
