@import "../../theme/colors.scss";

.btn-social {
  position: relative;
  width: 100%;
  margin-top: 10px;
  padding: 0;
  background-color: $base-white-color;
  border-radius: 1px;
  vertical-align: middle;
  text-align: center;
  cursor: pointer;

  .wrapper {
    display: flex;
    width: 100%;
    height: 100%;
    padding: 0;
    border: 1px solid transparent;
    color: $base-white-color;
    cursor: pointer;
    outline: none;
  }

  .icon {
    width: 15px;

    height: 23px;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
  }
  .social-text {
    width: 100%;
    align-self: center;
    text-align: center;
  }
}
