    position: relative;
    display: flex;
    margin-top: 36px;
  }
  
  .slick-track:before,
  .slick-track:after {
    display: table;
  
    content: '';
  }
  
  .slick-track:after {
    clear: both;
  }
  
  .slick-loading .slick-track {
    visibility: hidden;
  }
  
  .slick-slide {
    display: none;
    float: left;
  
    height: 100%;
    min-height: 1px;
  }
  
