.btn{position:relative;border:none;padding:12px;border-radius:4px;-webkit-user-select:none;-ms-user-select:none;user-select:none}.btn:hover{transition:all 0.15s ease-in-out 0s}.btn-transparent{background-color:#fff;color:#1976d2;transition:all 0.15s ease-in-out 0s}.btn-outline{background-color:#fff}.btn-primary{border:1px solid #1976d2;color:#1976d2}.btn-primary:hover{background-color:#1976d2;border-color:#1976d2;color:#fff}.btn-danger{color:#e11242;border:1px solid #e11242}.date-range{margin-left:4px;display:flex;position:relative;background:#ffffff;height:48px;border:none;padding:10px 10px 10px 30px;transition-duration:0.3s}.date-range:hover{box-shadow:0 0 0 1px #d59c00 inset}.date-range .date-icon{position:absolute;top:25%;left:16px;width:18px;z-index:1}.date-range .input{width:100%;max-width:112px;margin:0;padding:0;outline:none;border:none;background-color:transparent;z-index:1;left:0px;height:24px;line-height:24px;vertical-align:top;transition-property:color;transition-duration:0.3s;color:#000000;font-size:14px;font-weight:500;text-align:center}.date-range .date-range-arrow{line-height:24px;color:#fff}.form-title{font-size:52px;line-height:1;text-align:center;text-transform:uppercase;margin-top:0;margin-bottom:24px;color:#3f5f75}.form-subtitle{color:#3f5f75;font-size:24px;text-align:center;margin-bottom:36px}.form{display:flex;flex-direction:column;margin-top:10px}.form-border-error{border:1px solid #f41931}.checkbox-container{display:flex;flex-direction:row;align-items:center;margin-left:8px}.checkbox-container .label{position:relative;font-size:14px;margin:0;color:#1976d2;cursor:pointer}.checkbox-container .label:before{content:'';-webkit-appearance:none;background-color:transparent;border:2px solid #1976d2;box-shadow:0 1px 2px rgba(0,0,0,0.05),inset 0px -15px 10px -12px rgba(0,0,0,0.05);padding:8px;display:inline-block;position:relative;vertical-align:middle;border-radius:4px;cursor:pointer;margin-right:8px;margin-bottom:2px}.checkbox-container .checkbox:checked+label:after{content:'';display:block;position:absolute;top:2px;left:7px;width:6px;height:14px;border:solid #1976d2;border-width:0 2px 2px 0;transform:rotate(45deg)}.checkbox-container .checkbox{padding:0;height:auto;height:initial;width:auto;width:initial;margin-bottom:0;display:none;cursor:pointer}.checkbox-container .error-checkbox ~ .label{color:#f41931}.checkbox-container .error-checkbox ~ .label:before{border:2px solid #f41931}html{position:relative;min-height:100%}body{font-family:"Raleway",sans-serif;margin:0;padding:0}ol,ul{margin-top:0;list-style:none;outline:0;padding:0}a{color:#1976d2;text-decoration:none;transition:all 0.3s}a:hover{opacity:0.7}button{cursor:pointer;transition:all 0.5s}button:hover{opacity:0.9}*,*::before,*::after{box-sizing:border-box}input::-webkit-outer-spin-button,input::-webkit-inner-spin-button{-webkit-appearance:none;margin:0}.container{width:100%;padding-top:60px}.inner{width:94%;max-width:1120px;margin:0 auto}.container-page{max-width:960px;width:100%;margin:80px auto 0}

