$colors: (
  primary: #1976d2,
  secondary: #3b5998,
  info: #4BBC56,
  info-hover: #5DE76A,
  danger: #e11242,
  grown: #D9E4E9,
  white: #FFFFFF,
  gray: #333333,
  black: #0D1D25,
  green: #7DF1CF,
  yellow: #febb02
);

$grays: (
  50: #fafafa,
  100: #f5f5f5,
  200: #eeeeee,
  300: #e0e0e0,
  400: #bdbdbd,
  500: #9e9e9e,
  600: #757575,
  700: #616161,
  800: #424242,
  900: #212121
);

