@import "../../theme/colors";

.home-list {
  padding: 50px 0;

  .title-sec {
    font-family: 'PT Serif', serif;
    font-size: 30px;
    line-height: 1.7;
    font-weight: 600;
    margin: 0;
  }
}

.list {
  display: flex !important;
  justify-content: space-between;
  margin: 24px 0 0;
  padding: 0;
}

.destinations {
  padding: 0 0 70px;
  .destination-image {
    background-size: cover;
    background-position: center center;
    height: 124px;
    width: 124px;
    margin: 0 auto;
    border-radius: 50%;
    box-shadow: 0 2px 5px 0 rgba(0,0,0,0.16),0 2px 10px 0 rgba(0,0,0,0.12);
    transition: all 0.3s;
  }

  .destination-image:hover {
    transform: scale(1.1);
    box-shadow: 0 2px 9px 2px rgba(0,0,0,.15);
  }

  .title-sec {
    font-family: 'PT Serif', serif;
    font-size: 30px;
    line-height: 1.7;
    font-weight: 600;
    margin: 0;
  }

  .destination-content {
    padding-top: 12px;

    .title {
      display: block;
      text-align: center;
      font-size: 16px;
      font-weight: 700;
      line-height: 22px;
      margin: 0;
    }

    .subtitle {
      font-size: 12px;
      line-height: 16px;
      font-weight: 400;
      text-align: center;
      margin: 0;
      color: #6b6b6b;
    }
  }
}
