@import "./theme/main.scss";

html {
  position: relative;
  min-height: 100%;
}

body {
  font-family: $primary-font;
  margin: 0;
  padding: 0;
}

ol, ul {
  margin-top: 0;
  list-style: none;
  outline: 0;
  padding: 0;
}

a {
  color: $text-primary-color;
  text-decoration: none;
  transition: all 0.3s;
}

a:hover {
  opacity: 0.7;
}

button {
  cursor: pointer;
  transition: all 0.5s;
}

button:hover {
  opacity: 0.9;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0; 
}

.container {
  width: 100%;
  padding-top: $header-height-pc;
}

.inner {
  width: 94%;
  max-width: 1120px;
  margin: 0 auto;
}

.container-page {
  max-width: 960px;
  width: 100%;
  margin: 80px auto 0;
}
