@charset 'UTF-8';

/*
  Author: node_modules/slick-carousel/slick/slick-theme.css
*/

/* Slider */
.slick-loading .slick-list {
  background: red center center no-repeat;
}
.slick-track {
  justify-content: left;
}

/* Arrows */
.slick-prev,
.slick-next {
  font-size: 0;
  line-height: 0;
  position: absolute;
  top: 45%;
  display: block;
  width: 32px;
  height: 32px;
  background: #FFF;
  padding: 0;
  -webkit-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  transform: translate(0, -50%);
  cursor: pointer;
  color: transparent;
  border: none;
  outline: none;
  z-index: 1;
}

.slick-prev:hover,
.slick-prev:focus,
.slick-next:hover,
.slick-next:focus {
  color: transparent;
  outline: none;
}

.slick-prev:hover:before,
.slick-prev:focus:before,
.slick-next:hover:before,
.slick-next:focus:before {
  opacity: 1;
}

.slick-prev.slick-disabled,
.slick-next.slick-disabled {
  visibility: hidden;
}

.slick-prev:before,
.slick-next:before {
  width: 16px;
  height: 16px;
  line-height: 1;
  display: block;
  margin: 0 auto;
  opacity: .75;
  color: white;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.slick-prev {
  left: 0;
  -webkit-border-top-right-radius: 4px;
  -webkit-border-bottom-right-radius: 4px;
  -moz-border-radius-topright: 4px;
  -moz-border-radius-bottomright: 4px;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
}

[dir='rtl'] .slick-prev {
  right: 0;
  left: auto;
}

.slick-prev:before {
  content: ' ';
  background-image: url("../../assets/images/arrow-left.svg");
}

[dir='rtl'] .slick-prev:before {
  content: '→';
}

.slick-next {
  right: 0;
  -webkit-border-top-left-radius: 4px;
  -webkit-border-bottom-left-radius: 4px;
  -moz-border-radius-topleft: 4px;
  -moz-border-radius-bottomleft: 4px;
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
}

[dir='rtl'] .slick-next {
  right: auto;
  left: 0;
}

.slick-next:before {
  content: ' ';
  background-image: url('../../assets/images/arrow-right.svg');
}

[dir='rtl'] .slick-next:before {
  content: '←';
}

/* Dots */
.slick-dotted.slick-slider {
  margin-bottom: 30px;
}

.subscription-dots {
  position: absolute;
  top: 0;

  display: block;

  width: 100%;
  padding: 0;
  margin: 0;

  list-style: none;

  text-align: center;
}

.subscription-dots li {
  position: relative;

  display: inline-block;

  width: 10px;
  height: 10px;
  border-radius: 50%;
  margin: 0 5px;
  padding: 0;

  cursor: pointer;
  background-color: #fff;
  border: solid 1px #e6e6e6;
}

.subscription-dots .slick-active {
  background-color: #77aa22;
}

.subscription-dots li button {
  font-size: 0;
  line-height: 0;

  display: block;

  width: 10px;
  height: 10px;

  cursor: pointer;

  color: transparent;
  border: 0;
  outline: none;
  background: transparent;
}

.subscription-dots li button:hover,
.subscription-dots li button:focus {
  outline: none;
}

.subscription-dots li button:hover:before,
.subscription-dots li button:focus:before {
  opacity: 1;
}

.subscription-dots li button:before {
  font-family: 'slick';
  font-size: 6px;
  line-height: 20px;

  position: absolute;
  top: 0;
  left: 0;

  width: 20px;
  height: 20px;

  content: '';
  text-align: center;

  opacity: .25;
  color: black;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.subscription-dots li.slick-active button:before {
  opacity: .75;
  color: black;
}

.slick-slide {
  opacity: 0.5;
}

.slick-slide.slick-active {
  opacity: 1;
}
