@import "../../theme/colors";

.card {
  padding: 0 8px;

  .card-image {
    background-size: cover;
    background-position: center center;
    height: 240px;
    border-radius: 2px;
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
  }
  
  .card-content {
    padding: 8px 0;
  
    .title {
      font-size: 14px;
      line-height: 20px;
      font-weight: 400;
      margin: 0;
      color: $base-blue-color;
    }
  
    .subtitle {
      font-size: 14px;
      line-height: 20px;
      font-weight: 400;
      margin: 0;
      color: map-get($grays, 700);
    }
  
    .price {
      font-size: 14px;
      font-weight: 600;
      line-height: 20px;
      margin-bottom: 0;
      margin-top: 8px;
      color: $base-black-color;
    }
  
    .review {
      display: flex;
      align-items: center;
      margin-top: 4px;
  
      .title {
        font-size: 14px;
        line-height: 20px;
        font-weight: 500;
        color: $base-blue-color;
      }
  
      .text {
        font-size: 12px;
        line-height: 18px;
        font-weight: 400;
        margin-top: 2px;
        margin-left: 8px;
        color: map-get($grays, 700);
      }
    }
  
    .review-score {
      width: 28px;
      height: 28px;
      font-size: 14px;
      line-height: 2;
      font-weight: 500;
      display: flex;
      justify-content: center;
      vertical-align: baseline;
      border-radius: 6px 6px 6px 0;
      background: $base-blue-color;
      color: $base-white-color;
      margin-right: 16px;
      padding: 0 16px;
    }
  }
}
